import React from 'react';
import { NavLink } from 'react-router-dom';
import Events from '../Events';

export default class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dropdown: false
        }
    }

    render() {
        const {
            Oauth2,
            MainArray,
            SetAppState
        } = this.props, {
            dropdown
        } = this.state;

        return <header className="App-Header">
            <NavLink 
                to="/" 
                className="Header-Link"
                onClick={() => SetAppState({ Cerner: null })}
            >UMX-DX App</NavLink>
            {Oauth2 ? (
                <div className="Header-Navbar">
                    <button
                        className="Header-Navbar-Button"
                        onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                    >Menu</button>
                    {dropdown ? (
                        <div className="Header-Navbar-List">
                            {MainArray.map((title, i) => {
                                return <NavLink key={i}
                                    to={`/${Events.route(title)}`} 
                                    className="Header-Navbar-Link"
                                    activeClassName="Header-Navbar-Link-Active"
                                    onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                                >{title}</NavLink>
                            })}
                            <NavLink 
                                to="/" 
                                className="Header-Navbar-Link"
                                onClick={() => {
                                    this.setState(prevState => ({ dropdown: !prevState.dropdown }))
                                    SetAppState({
                                        Oauth2: null,
                                        Cerner: null,
                                        MainIndex: 0,
                                        DisplayCount: 25,
                                        DisplayIndex: 0
                                    });
                                }}
                            >Log Out</NavLink>
                        </div>
                    ) : null }
                </div>
            ) : (
                <div className="Header-Navbar">
                    <button
                        className="Header-Navbar-Button"
                        onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                    >Launch</button>
                    {dropdown ? (
                        <div className="Header-Navbar-List">
                            <NavLink 
                                to="/launch-patient" 
                                className="Header-Navbar-Link"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Patient</NavLink>
                            <NavLink 
                                to="/launch-provider" 
                                className="Header-Navbar-Link"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Provider</NavLink>
                        </div>
                    ) : null }
                </div>
            )}
        </header>
    }
};